import uniCalendar from '@/components/uni-calendar/uni-calendar.vue';
function nowDate(){
	let nowDate = new Date();
	return nowDate.getFullYear() + '-' + ('0'+(nowDate.getMonth() + 1)).slice(-2) + '-' + ('0'+nowDate.getDate()).slice(-2);
}
function nowTime(){
	let nowDate = new Date();
	return ('0'+nowDate.getHours()).slice(-2)+':'+ ('0'+nowDate.getMinutes()).slice(-2);
}
export default {
	components: {
		uniCalendar
	},
	data(){
		return {
			action:1,
			date:'',
			mapshow:false,
			dateInfo: {
				date: nowDate(),
				startDate: nowDate(),
				time:nowTime(),
				endDate: '',
				lunar: false,
				range: false,
				insert: false,
				selected: []
			},
				latitude: 39.119929,
				longitude: 117.752705,
				covers: [{
					latitude: 39.9085,
					longitude: 116.39747,




					iconPath: '../../../static/location.png',

				}],
			polyline:[{
					points: [{
						latitude: 39.9085,
						longitude: 116.39747,
					},{
						latitude: 39.90,
						longitude: 116.39,
					}
					],
					color: '#CCFFFF',
					width: 5,
					dottedLine: true,
					arrowLine: true,
					borderColor: '#CC0000',
					borderWidth: 3
				}
			],
			polygons:{
			
			}
		}
	},
	methods:{
		active(num){
			this.action = num
		},
		regionchange(e){
			uni.getCenterLocation({
				success:function(value){
					console.log(value)
				}
			})
		},
		tap(e){
			this.polyline[0].points.push({
		        latitude: e.detail.latitude,
		        longitude: e.detail.longitude
		    })
			var me = this
			uni.chooseLocation({
				success:function(e){
					console.log(e)
					me.latitude = e.latitude
					me.longitude = e.longitude
					me.covers[0].longitude = e.longitude;
					me.covers[0].latitude = e.latitude;
				}
			})
		},
		confirm(e) {
			console.log('confirm 返回:', e)
			this.dateInfo.date = e.fulldate;
			this.dateInfo.time = e.time
			this.mapshow=false;
		},
		close(){
			this.mapshow=false;
		},
		open() {
			this.$refs.calendar.open();
			this.mapshow=true;
		}
	},
	onLoad(){
		var me = this;
		uni.getLocation({
			type:'gcj02',
			success:function(e){
				console.log(e)
				me.latitude = e.latitude
				me.longitude = e.longitude
				me.covers[0].longitude = e.longitude;
				me.covers[0].latitude = e.latitude;
			}
		})
	}
}
